@import '../mixins';

.split-section {
    padding: 120px 125px;
    margin-bottom: 150px;
    height: 484px;
    overflow: visible;

    &__body {
        &__heading {
            color: #FFF;
            font-family: Inter Tight;
            font-size: 48px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
            letter-spacing: 0.96px;
            margin-bottom: 10px;

            span {
                color: #AAAAAA;
            }
        }

        &__paragarph {
            color: var(--Light-Gray-1, #AAA);
            font-family: Inter Tight;
            font-size: 24px;
            font-style: normal;
            font-weight: 400;
            line-height: 30px;
            letter-spacing: 0.48px;
        }
    }

    &__img {

        position: relative;
        top: 10px;
        z-index: 999;
        @include respond(md-tab-land){
            top: 100px;
        }
    }

    @include respond(sm-tab-port) {
        padding: 0 16px 96px 16px;
        margin-top: 300px;
        margin-bottom: 0;
        height: auto;


        &__img {
            top: -40%;
        }
    }

    @include respond(phone) {
        &__body {
            &__heading {
                font-size: 32px;
                margin-bottom: 32px;
            }

            &__paragarph {
                font-size: 18px;
            }
        }
    }
}